type TraceVerbosity = 'none' | 'trace' | 'stack' | 'fullStack';

const defaultTraceVerbosity: TraceVerbosity = 'trace';

const getTrace = (traceVerbosity: TraceVerbosity = defaultTraceVerbosity) => {
  const fullStack = new Error().stack?.split('\n').slice(3);
  const stack = fullStack?.map((v) => v.match(/at ([^ ]+) /)?.[1]) ?? [];

  const __trace = stack.reduce(
    (acc, tr, i) => acc ?? (tr ? (i > 0 ? `~${tr}` : tr) : undefined),
    undefined,
  );

  return traceVerbosity === 'none'
    ? {}
    : {
        __trace,
        ...(traceVerbosity === 'stack' || traceVerbosity === 'fullStack' ? { __stack: stack } : {}),
        ...(traceVerbosity === 'fullStack' ? { __fullStack: fullStack } : {}),
      };
};

let LOGGING_ENABLED = false;
export const enableLogging = (enable: boolean) => (LOGGING_ENABLED = enable);

export type LogFn = typeof console.log;

export const log = (
  data: unknown,
  traceVerbosity: TraceVerbosity = defaultTraceVerbosity,
  // eslint-disable-next-line no-console
  logFn: LogFn = console.log,
  message = '',
) =>
  LOGGING_ENABLED &&
  logFn(message, {
    ...getTrace(traceVerbosity),
    ...(typeof data === 'object' ? data : { data }),
  });

export const error = (
  message: string,
  data: unknown,
  traceVerbosity: TraceVerbosity = defaultTraceVerbosity,
) =>
  // eslint-disable-next-line no-console
  log(data, traceVerbosity, console.error, message);
