import { Dispatch, useEffect, useReducer } from 'react';
import { lameDec, lameEnc } from 'utils/crypto';

export type DestroyPersistentState = () => void;

export const getIsStateFnFor =
  <S>(validState: S) =>
  (state: unknown): state is S => {
    return (
      typeof state === 'object' &&
      state !== null &&
      Object.keys(state).length === Object.keys(validState as Record<string, unknown>).length &&
      Object.entries(state).every(
        ([key, value]) => typeof (validState as Record<string, unknown>)[key] === typeof value,
      )
    );
  };

export const usePersistedReducer = <State, Action>(
  stateKey: string,
  reducer: (state: State, action: Action) => State,
  initialState: State,
  initFn?: (initialState: State) => State,
  isStateFn?: (state: unknown) => state is State,
): [State, Dispatch<Action>, DestroyPersistentState] => {
  const init = (): State => {
    const initState = typeof initFn !== 'undefined' ? initFn(initialState) : initialState;
    const isState = typeof isStateFn !== 'undefined' ? isStateFn : getIsStateFnFor(initialState);

    try {
      const persistedStateStr = lameDec(window.localStorage.getItem(lameEnc(stateKey)) ?? '');
      const persistedState = persistedStateStr ? JSON.parse(persistedStateStr) : undefined;
      return isState(persistedState) ? persistedState : initState;
    } catch (e) {
      return initState;
    }
  };

  const [state, dispatch] = useReducer(reducer, undefined, init);
  const stateStr = JSON.stringify(state);
  const dts = (state as { dts: boolean }).dts;

  useEffect(() => {
    window.localStorage.setItem(lameEnc(stateKey), lameEnc(stateStr));
    if (dts === true) {
      window.localStorage.setItem(stateKey, stateStr);
    } else {
      window.localStorage.removeItem(stateKey);
    }
  }, [stateKey, stateStr, dts]);

  const destroy = () => {
    window.localStorage.removeItem(stateKey);
    window.localStorage.removeItem(lameEnc(stateKey));
  };

  return [state, dispatch, destroy];
};
