import { getRandomIntInclusive } from './crypto';
import { log } from './log';

export const stratificationGroups = ['treatment', 'control'] as const;
export type StratificationGroup = (typeof stratificationGroups)[number];
export const isStratificationGroup = (sg: unknown): sg is StratificationGroup =>
  typeof sg === 'string' && stratificationGroups.includes(sg as StratificationGroup);

export type TypeOrUnset<T> = T | 'unset';
export type StratificationGroupOrUnset = TypeOrUnset<StratificationGroup>;

export const getStratification = (
  currentGroup: StratificationGroupOrUnset,
  controlPercent: number,
): StratificationGroup => (currentGroup !== 'unset' ? currentGroup : stratify(controlPercent));

export const stratify = (controlPercent: number) =>
  getRandomIntInclusive(0, 99) > controlPercent ? 'treatment' : 'control';

export const testStratification = (controlPercent: number, testCount: number) => {
  let treatmentCount = 0;
  let controlCount = 0;

  for (let i = 0; i < testCount; i++) {
    const testGroup = stratify(controlPercent);
    testGroup === 'treatment' ? treatmentCount++ : controlCount++;
  }

  const totalCount = treatmentCount + controlCount;
  const actualTreatmentPercent = (treatmentCount / totalCount) * 100;
  const actualControlPercent = (controlCount / totalCount) * 100;

  const results = {
    treatmentCount,
    controlCount,
    totalCount,
    actualTreatmentPercent,
    actualControlPercent,
    expectedControlPercent: controlPercent,
    deviationPercent: Math.abs(controlPercent - actualControlPercent),
  };

  log(results);

  return results;
};
