import { ImgHTMLAttributes } from 'react';
import { RequiredProps } from 'types/util';

export const personas = ['Sightshare', 'Kuma', 'Robin', 'expert'] as const;
export type Persona = (typeof personas)[number];
export const isPersona = (persona: unknown): persona is Persona =>
  typeof persona === 'string' && personas.includes(persona as Persona);

export interface ChatProfile {
  id: Persona;
  name: string;
  avatar: string;
}

export type GetChatProfileFn = (persona: Persona) => ChatProfile;

export interface ChatMessage {
  type: 'message';
  from: Persona;
  messages: Array<string | Image>;
}

export const isChatMessage = (msg: unknown): msg is ChatMessage =>
  typeof msg === 'object' &&
  msg !== null &&
  (msg as ChatMessage).type === 'message' &&
  isPersona((msg as ChatMessage).from) &&
  Array.isArray((msg as ChatMessage).messages);

export interface ChatResponse {
  type: 'response';
  messages: string[];
}

export interface GameResponseOption {
  id: string;
  label: string;
  answer?: ChatMessage;
  nextStepId?: string;
}

export const isGameResponseOption = (opt: unknown): opt is GameResponseOption =>
  typeof opt === 'object' &&
  opt !== null &&
  typeof (opt as GameResponseOption).id === 'string' &&
  (opt as GameResponseOption).id !== '' &&
  typeof (opt as GameResponseOption).label === 'string' &&
  (opt as GameResponseOption).label !== '' &&
  ((opt as GameResponseOption).answer === undefined ||
    isChatMessage((opt as GameResponseOption).answer));

export interface GameResponse {
  prompt?: string;
  options: GameResponseOption[];
  randomise?: boolean; // default true
  followUp?: ChatMessage;
  multiChoice?: boolean; // default false
}

export const isGameResponse = (resp: unknown): resp is GameResponse =>
  typeof resp === 'object' &&
  resp !== null &&
  Array.isArray((resp as GameResponse).options) &&
  (resp as GameResponse).options.every((opt) => isGameResponseOption(opt));

export type Image = RequiredProps<ImgHTMLAttributes<HTMLImageElement>, 'src'>;

export interface Post {
  id: string;
  img: Image;
  likes: number;
  shares: number;
  views: number;
}

export type ChatMessages = ChatMessage | ChatResponse;

export interface Profile {
  name: string;
  avatar?: string;
  friends: number;
  initialFriends?: number;
  followers: number;
  loaded: boolean;
}

