import { log } from 'utils';
import {
  ChatMessage,
  GameContentConfig,
  GameStepContentConfig,
  SurveyConfigOld,
} from 'types/configs';
import { gamePost4Content as config } from '../game/content';
import { preStyles } from './import-config';
import { ConfigJson, ConfigJsonItem, configJsonItemKeys, isGameStepContentConfig } from './types';

const ExportConfig = () => {
  try {
    const ret = transformContentConfig(config);

    log({
      ret,
    });

    return (
      <>
        <pre style={preStyles}>
          <code>{JSON.stringify(ret, null, 2)}</code>
        </pre>

        <hr />

        {/* <pre style={preStyles}>
          <code>{JSON.stringify(inp[0], null, 2)}</code>
        </pre> */}
      </>
    );
  } catch (err) {
    return (
      <pre style={preStyles}>
        <code>{JSON.stringify(err, null, 2)}</code>
      </pre>
    );
  }
};

const buildJsonItem = (configJsonItem: Partial<ConfigJsonItem>): ConfigJsonItem =>
  configJsonItemKeys.reduce(
    (acc, key) => ({ ...acc, [key]: configJsonItem[key] || '' }),
    {} as ConfigJsonItem,
  );

export const transformContentConfig = (config: GameContentConfig): ConfigJson =>
  Object.values(config).reduce(
    (acc, stepConfig) => [
      ...acc,
      ...transformMessages(stepConfig),
      ...transformResponses(stepConfig),
    ],
    [] as ConfigJson,
  );

export const transformOldConfig = (config: SurveyConfigOld): ConfigJson =>
  config.reduce(
    (acc, stepConfig) =>
      isGameStepContentConfig(stepConfig)
        ? [...acc, ...transformMessages(stepConfig), ...transformResponses(stepConfig)]
        : acc,
    [] as ConfigJson,
  );

const transformMessages = ({ id, messages }: GameStepContentConfig) => {
  const msgs = messages.filter(({ type }) => type === 'message') as ChatMessage[];

  const jsonItems = msgs.reduce((ret, chatMsg) => {
    const flatMsgs = chatMsg.messages.map((msg) =>
      buildJsonItem({
        ID: id,
        From: chatMsg.from,
        Message: typeof msg === 'object' ? msg.alt || '' : msg,
        Image: typeof msg === 'object' ? msg.src : '',
      }),
    );

    const jsonMsgs = [...ret, ...flatMsgs];

    return jsonMsgs;
  }, [] as ConfigJson);

  return jsonItems;
};

const transformResponses = ({ id, response }: GameStepContentConfig) => {
  if (!response) return [];

  const jsonItems = response.options.reduce((ret, opt, i) => {
    const optJson = buildJsonItem({
      ID: id,
      'Answer ID': opt.id,
      Answer: opt.label,
      'Next step': opt.nextStepId,
      Randomise: i === 0 && response.randomise === false ? 'false' : undefined,
      'Multi-Choice': i === 0 && response.multiChoice === true ? 'true' : undefined,
    });

    const optFollowUpMsgs = opt.answer?.messages.map((msg) =>
      buildJsonItem({
        ID: id,
        From: opt.answer?.from,
        'Answer ID': opt.id,
        'Follow up': typeof msg === 'object' ? msg.alt || '' : msg,
        Image: typeof msg === 'object' ? msg.src : '',
      }),
    );

    const jsonMsgs = [...ret, optJson, ...(optFollowUpMsgs || [])];

    return jsonMsgs;
  }, [] as ConfigJson);

  const followUpMsgs = response.followUp?.messages.map((msg) =>
    buildJsonItem({
      ID: id,
      From: response.followUp?.from,
      'Follow up': typeof msg === 'object' ? msg.alt || '' : msg,
      Image: typeof msg === 'object' ? msg.src : '',
    }),
  );

  return [...jsonItems, ...(followUpMsgs || [])];
};

export default ExportConfig;
