import ResponseButton from 'components/ResponseButton';
import SurveyImageOptions from 'components/SurveyImageOptions';
import SurveyOptions from 'components/SurveyOptions';
import SurveyRange from 'components/SurveyRange';
import SurveyRangeGroup from 'components/SurveyRangeGroup';
import SurveyText from 'components/SurveyText';
import { submitData } from 'pages/SightSharePage';
import { Dispatch, useCallback, useEffect } from 'react';
import { Response, SurveyStepConfig } from 'types/configs';
import { Action } from 'types/reducer';
import { Progress, State } from 'types/state';
import { log } from 'utils';

interface Props {
  config: SurveyStepConfig;
  state: State;
  progress: Progress;
  dispatch: Dispatch<Action>;
  moduleClasses: (classes: string) => string;
}

const SurveyStep = ({ config, state, progress, dispatch, moduleClasses }: Props) => {
  const {
    id,
    type,
    label,
    nextLabel,
    backLabel,
    hideBack,
    hideNext,
    hideProgress,
    nextStep,
    redirectUrlFn,
  } = config;
  const { step, responses, validSteps } = state;

  const value = responses[id]?.response?.[0];
  const valid = validSteps[step];

  const handleNext = () => {
    const timestamp = new Date().toISOString();
    const { friends } = state.profile;

    const payload =
      type === 'range-group'
        ? config.ranges.map((cnf) => ({
            id: cnf.id,
            value: state.responses[cnf.id]?.response?.[0],
            timestamp,
            friends,
          }))
        : {
            id,
            value,
            timestamp,
            friends,
          };

    log({ id, value, valid, type, payload });

    submitData({
      headers: {
        sid: state.secId,
        gid: state.gameId,
        rid: state.respId,
      },
      payload,
    });
    dispatch(nextStep ? { type: 'GOTO_STEP_ID', id: nextStep } : { type: 'NEXT' });
  };

  const handleBack = () => dispatch({ type: 'BACK' });
  const setResponse = useCallback(
    (
      id: Response['id'],
      response: Response['response'][0],
      invalid: Response['invalid'] = false,
    ) => {
      dispatch({
        type: 'RESPONSE',
        response: { id, response: [response], invalid },
      });

      dispatch({
        type: 'STEP_SET_VALID',
        step,
        valid: Boolean(response) && !invalid,
      });
    },
    [dispatch, step],
  );

  const setResponseForId = useCallback(
    (response: Response['response'][0], invalid?: Response['invalid']) =>
      setResponse(id, response, invalid),
    [id, setResponse],
  );

  useEffect(() => {
    if (type === 'info' && !valid) {
      setResponseForId('info-ok');
    }
  }, [type, valid, setResponseForId]);

  useEffect(() => {
    if (typeof redirectUrlFn === 'function') {
      const redirectUrl = redirectUrlFn(state);
      log({ op: 'redirect', respId: state.respId, redirectUrl });

      if (redirectUrl) {
        const timeoutId = setTimeout(() => window.location.replace(redirectUrl), 3000);
        return () => {
          log({ op: 'clearTimeout', timeoutId });
          clearTimeout(timeoutId);
        };
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [redirectUrlFn]);

  // console.log('config', config);
  // console.log('responses', responses);
  // console.log('value', value);

  return (
    <>
      <div className={moduleClasses('survey-question')}>
        {typeof label === 'function' ? label(state) : label}
      </div>

      <div className={moduleClasses('survey-responses')}>
        {type === 'options' && (
          <SurveyOptions
            config={config}
            selected={value}
            setResponse={setResponseForId}
            next={handleNext}
            moduleClasses={moduleClasses}
          />
        )}

        {type === 'image-options' && (
          <SurveyImageOptions
            config={config}
            selected={value}
            setResponse={setResponseForId}
            next={handleNext}
            moduleClasses={moduleClasses}
          />
        )}

        {type === 'range' && (
          <SurveyRange
            config={config}
            state={state}
            selected={value}
            setResponse={setResponseForId}
            next={handleNext}
            moduleClasses={moduleClasses}
          />
        )}

        {type === 'range-group' && (
          <SurveyRangeGroup
            config={config}
            state={state}
            dispatch={dispatch}
            moduleClasses={moduleClasses}
          />
        )}

        {type === 'text' && (
          <SurveyText
            config={config}
            response={responses[id]}
            setResponse={setResponseForId}
            next={handleNext}
            moduleClasses={moduleClasses}
          />
        )}
      </div>

      <div className={moduleClasses('survey-footer')}>
        {!hideNext && (
          <div className={moduleClasses('survey-button-next')}>
            {/* {value && valid && <ResponseButton onClick={handleNext}>Next</ResponseButton>} */}
            {
              <ResponseButton
                onClick={handleNext}
                disabled={!valid}
                keyboardShortcut={['Enter', 'PageDown']}
              >
                {nextLabel || 'Next'}
              </ResponseButton>
            }
          </div>
        )}
        {!hideProgress && (
          <div className={moduleClasses('survey-progress')}>
            {progress.current} / {progress.total}
          </div>
        )}
        {!hideBack && (
          <div className={moduleClasses('survey-button-back')}>
            {/* {step > 0 && (
            // <button className={moduleClasses('link')} onClick={handleBack}>
            //   Back
            // </button>
            <ResponseButton onClick={handleBack}>Back</ResponseButton>
          )} */}
            <ResponseButton onClick={handleBack} disabled={step === 0} keyboardShortcut="PageUp">
              {backLabel || 'Back'}
            </ResponseButton>
          </div>
        )}
      </div>
      {/* <div>
        <pre>{JSON.stringify(state, null, 2)}</pre>
      </div> */}
    </>
  );
};

export default SurveyStep;
