// Update/bump up every time State structure is changed in order to
// invalidate any existing persisted states on the next (re)load
// This should be the only place where the version is explicitely
// stated, everything else should use the `stateVersion` const or

import { ChatMessages, GameResponseOption, Post, Profile, Response } from 'types/configs';
import { log } from 'utils';
import { StratificationGroupOrUnset } from 'utils/stratification';

// `typeof stateVersion` as reference to improve maintenance.
export const STATE_VERSION = '202401260949';

export interface State {
  version: typeof STATE_VERSION;
  hash: string;
  step: number;
  responses: {
    [id: string]: Response;
  };
  config: {
    [id: string]: {
      responseOptions: GameResponseOption[];
    };
  };
  validSteps: boolean[];
  profile: Profile;
  post: Post;
  messages: ChatMessages[];
  friend1: string;
  friend2: string;
  variables: {
    lostFriends: number;
    [key: string]: string | number | boolean;
  };
  dte: boolean;
  dts: boolean;
  stratificationGroup: StratificationGroupOrUnset;
  respId: string;
  gameId: string;
  secId: string;
  isSubmitting: boolean;
}

export const initialState: State = {
  version: STATE_VERSION,
  hash: '',
  step: 0,
  responses: {},
  config: {},
  validSteps: [],
  profile: {
    name: '',
    friends: -1,
    followers: -1,
    loaded: false,
  },
  post: {
    id: '',
    img: { src: '' },
    likes: -1,
    shares: -1,
    views: -1,
  },
  messages: [],
  friend1: '',
  friend2: '',
  variables: {
    lostFriends: 0,
  },
  dte: process.env.NODE_ENV === 'development',
  dts: process.env.NODE_ENV === 'development',
  stratificationGroup: 'unset',
  respId: '',
  gameId: '',
  secId: '',
  isSubmitting: false,
};

const validState = initialState as unknown as Record<string, unknown>;

export const isState = (state: unknown): state is State => {
  const isState =
    typeof state === 'object' &&
    state !== null &&
    Object.keys(state).length === Object.keys(validState).length &&
    Object.entries(state).every(([key, value]) => typeof validState[key] === typeof value);

  log({
    stateType: typeof state,
    stateNull: state === null,
    stateLength: Object.keys(state || {}).length,
    validLength: Object.keys(validState).length,
    keyTypesValid: Object.entries(state || {}).every(
      ([key, value]) => typeof validState[key] === typeof value,
    ),
    stateTypes: Object.entries(state || {}).map(([k, v]) => ({ [k]: typeof v })),
    validTypes: Object.entries(validState || {}).map(([k, v]) => ({ [k]: typeof v })),
    isState,
  });

  return isState;
};

export const isCurrentState = (state: unknown): state is State => {
  return isState(state) && state.version === validState.version;
};

export interface Progress {
  current: number;
  total: number;
}

export const getResponse = (state: State, responseId: string, defaultResponse = '') =>
  getResponses(state, responseId)[0] || defaultResponse;

export const getResponseFn =
  (responseId: string, defaultResponse = '') =>
  (state: State) =>
    getResponse(state, responseId, defaultResponse);

export const getResponses = (state: State, responseId: string) =>
  state.responses[responseId]?.response || [];
