import Img from 'components/Img';
import { Persona } from 'types/configs';

const avatarIds = ['profile-1', 'profile-2', 'profile-3', 'profile-4', 'profile-5'] as const;
type AvatarId = (typeof avatarIds)[number];
type Avatars = Record<AvatarId, string>;

const profileAvatars: Avatars = {
  'profile-1': 'profile-1.jpg',
  'profile-2': 'profile-2.jpg',
  'profile-3': 'profile-3.png',
  'profile-4': 'profile-4.jpg',
  'profile-5': 'profile-5.jpg',
};

type NpcAvatars = Record<Persona, string>;
const npcAvatars: NpcAvatars = {
  expert: 'expert.png',
  Kuma: 'kuma.png',
  Robin: 'robin.png',
  Sightshare: 'sight-share.png',
};

const getProfileAvatar = (avatarId?: string): string =>
  avatarId !== undefined && Object.keys(profileAvatars).includes(avatarId)
    ? profileAvatars[avatarId as keyof Avatars]
    : 'avatar.png';

const getNpcAvatar = (avatarId?: string): string => {
  return avatarId !== undefined && Object.keys(npcAvatars).includes(avatarId)
    ? npcAvatars[avatarId as keyof NpcAvatars]
    : 'expert.png';
};

type AvatarType = 'profile' | 'npc';
interface Props {
  avatar?: string;
  type?: AvatarType;
  alt?: string;
}

const getAvatar = (type: AvatarType) => (type === 'npc' ? getNpcAvatar : getProfileAvatar);

const Avatar = ({ avatar, type = 'profile', alt = 'Avatar' }: Props) => {
  return <Img src={`avatars/${getAvatar(type)(avatar)}`} alt={alt} />;
};
export default Avatar;
