import { SurveyConfig } from 'types/configs';
import { Action } from 'types/reducer';
import { State, initialState } from 'types/state';
import { log } from 'utils';

export const initWithConfig =
  (config: SurveyConfig) =>
  (initState: State): State => ({
    ...initState,
    validSteps: Array(Object.keys(config).length).fill(false),
  });

// export const init = initWithConfig(config);

type BasicType = string | number | boolean | unknown | null;
interface BasicDiff {
  '-': BasicType;
  '+': BasicType;
}

type Diff = BasicDiff | { [x: string]: Diff };

const diffSingle = (key: string, a: unknown, b: unknown): Diff => {
  if (typeof a === 'object' && typeof b === 'object') {
    const diff = diffObjects(a, b);
    return Object.keys(diff).length >= 0
      ? {
          [key]: diff,
        }
      : {};
  }

  const diff =
    a !== b
      ? {
          [key]: {
            '-': a,
            '+': b,
          },
        }
      : {};
  return diff;
};

const diffBasic = (objA: object | null, objB: object | null, initialDiff: Diff = {}): Diff => {
  const a = (objA ?? {}) as Record<string, unknown>;
  const b = (objB ?? {}) as Record<string, unknown>;

  return Object.keys(a ?? {}).reduce((acc, key) => {
    return {
      ...acc,
      ...diffSingle(key, a[key], b[key]),
    };
  }, initialDiff);
};

const diffObjects = (oldObj: object | null, newObj: object | null): Diff => {
  const diffOld = diffBasic(oldObj, newObj);
  const diff = diffBasic(oldObj, newObj, diffOld);

  return diff;
};

export const reducerWithConfig =
  (config: SurveyConfig) =>
  (state: State, action: Action): State => {
    // log('state', state);
    // log('action', action);

    const newState = reduce(config)(state, action);
    // log('newState', newState);

    const stateDiff = diffObjects(state, newState);

    // if (Object.keys(stateDiff).length > 0) {
    log({
      type: action.type,
      action,
      stateDiff,
    });
    // }

    return newState;
  };

export const getStepById = (config: SurveyConfig, id: string, fallback = -1) => {
  const step = Object.keys(config).findIndex((key) => key === id);
  const resStep = step !== -1 ? step : fallback;

  log({ id, fallback, step, resStep });
  return resStep;
};

const reduce =
  (config: SurveyConfig) =>
  (state: State, action: Action): State => {
    const { type } = action;
    // console.log(state);

    switch (type) {
      case 'RESPONSE':
        return {
          ...state,
          responses: {
            ...state.responses,
            [action.response.id]: action.response,
          },
        };

      case 'STEP_SET_VALID':
        return {
          ...state,
          validSteps: state.validSteps.map((valid, step) =>
            step === action.step ? action.valid : valid,
          ),
        };

      case 'NEXT':
        log({
          type,
          currentStep: state.step,
          isValid: state.validSteps[state.step],
          configLength: Object.keys(config).length,
          maybeNextStep: state.step + 1,
          actualNextStep: !state.validSteps[state.step] ? state.step : state.step + 1,
        });

        if (!state.validSteps[state.step]) return { ...state };
        return {
          ...state,
          step: state.step + 1,
        };

      case 'BACK':
        return {
          ...state,
          step: Math.max(state.step - 1, 0),
        };

      case 'RESET':
        return {
          ...initWithConfig(config)(initialState),
          dte: state.dte,
          respId: state.respId,
        };

      case 'GOTO_STEP':
        log({
          type,
          currentStep: state.step,
          nextStep: action.step,
          isValid: state.validSteps[state.step],
          responesLength: Object.keys(state.responses).length,
        });

        return {
          ...state,
          step: action.step,
        };

      case 'GOTO_STEP_ID':
        log({
          type,
          currentStep: state.step,
          nextStepId: action.id,
          nextStep: getStepById(config, action.id),
          isValid: state.validSteps[state.step],
          responesLength: Object.keys(state.responses).length,
        });

        return {
          ...state,
          step: getStepById(config, action.id),
        };

      case 'HANDLE_RESPONSE':
        return {
          ...state,
          // TODO: May not be needed
        };

      case 'RESET_RESPONSE':
        return {
          ...state,
          responses: {
            ...Object.entries(state.responses)
              .filter(([id]) => id !== action.id)
              .reduce((ret, [id, response]) => ({ ...ret, [id]: { ...response } }), {}),
          },
        };

      case 'CUSTOM_HANDLER':
        return action.handler(state, config);

      case 'SET_MESSAGES':
        return {
          ...state,
          messages: [...action.messages],
        };

      case 'SET_RESPONSE_OPTIONS':
        return {
          ...state,
          config: {
            ...state.config,
            [action.id]: {
              ...(state.config[action.id] ?? {}),
              responseOptions: action.responseOptions,
            },
          },
        };

      case 'DEC_FRIENDS':
        return {
          ...state,
          profile: {
            ...state.profile,
            friends: Math.round(state.profile.friends * 0.8),
          },
        };

      case 'RES_FRIENDS':
        return {
          ...state,
          profile: {
            ...state.profile,
            friends: state.profile.initialFriends ?? state.profile.friends,
          },
        };

      case 'EN_DT':
        return {
          ...state,
          dte: true,
        };

      case 'TG_DT':
        return {
          ...state,
          dts: state.dte && !state.dts,
        };

      case 'SET_SG':
        return {
          ...state,
          stratificationGroup:
            state.dte === true && state.dts === true ? action.sg : state.stratificationGroup,
        };

      case 'SET_LANDING':
        return {
          ...state,
          respId: action.respId,
          gameId: action.gameId,
          secId: action.secId,
        };

      case 'SET_SUBMITTING':
        return {
          ...state,
          isSubmitting: action.isSubmitting,
        };

      default:
        throw new Error(`Unknown action type: ${type}`);
    }
  };
