import { CSSProperties } from 'react';
import { ChatMessage, GameContentConfig, isPersona, Persona } from 'types/configs';
import {
  ConfigJsonValidationErrors,
  FlatConfig,
  FlatGameResponse,
  FlatStep,
  isConfigJson,
} from './types';
import { log } from 'utils';

import Post1 from './json/post-1.json';
import Post2 from './json/post-2.json';
import Post3 from './json/post-3.json';
import Post4 from './json/post-4.json';
import Post5 from './json/post-5.json';
import Post6 from './json/post-6.json';
import Post7 from './json/post-7.json';
import Post8 from './json/post-8.json';
import QuickTasks from './json/quick-tasks.json';

const configJsons = {
  Post1,
  Post2,
  Post3,
  Post4,
  Post5,
  Post6,
  Post7,
  Post8,
  QuickTasks,
};

const ImportConfigs = () => (
  <>
    {Object.entries(configJsons).map(([configId, configJson]) => (
      <ImportConfig key={configId} configId={configId} configJson={configJson} />
    ))}
  </>
);

interface ImportConfigProps {
  configId: string;
  configJson: unknown;
}

const ImportConfig = ({ configId, configJson }: ImportConfigProps) => {
  try {
    if (!isConfigJson(configJson)) {
      throw new ConfigJsonValidationErrors({ error: 'Invalid config' });
    }
  } catch (e) {
    const err = e as ConfigJsonValidationErrors;
    return (
      <div>
        <h1>{err.message}</h1>

        <pre style={preStyles}>
          <code>{JSON.stringify(err.errors, null, 2)}</code>
        </pre>
      </div>
    );
  }

  const configObj = configJson.reduce((cnf: FlatConfig, row, rowIndex) => {
    const getFromPersona = (from: string): Persona => {
      if (isPersona(from)) return from;
      return 'Sightshare';
    };

    const {
      ID: id,
      From: fromStr,
      Message: message,
      Image: image,
      'Answer ID': answerId,
      Answer: answer,
      'Follow up': followUp,
      'Next step': nextStep,
      Randomise: randomise,
      'Multi-Choice': multiChoice,
    } = row;
    const from = getFromPersona(fromStr);

    const isMessage = fromStr !== '' && (image !== '' || message !== '') && answerId === '';
    const isAnswer = answerId !== '' && answer !== '';
    const isAnswerFollowUp = answerId !== '' && fromStr !== '' && (followUp !== '' || image !== '');
    const isFollowUp = answerId === '' && fromStr !== '' && followUp !== '';
    const shouldNotRandomise = randomise === 'false';
    const isMultiChoice = multiChoice === 'true';

    const chatMessage: ChatMessage = {
      from,
      type: 'message',
      messages: [
        ...(Array.isArray(cnf[id]?.messages?.[from]?.messages)
          ? cnf[id].messages[from].messages
          : []),
        image ? { src: `${image}`, alt: message } : message,
      ],
    };

    const cnfResponse = cnf[id]?.response;
    const cnfOptions = cnfResponse?.options;
    const cnfAnswer = cnfOptions?.[answerId];
    const cnfNextStep = cnfAnswer?.nextStepId;
    const cnfAnswerFollowUp = cnfAnswer?.answer;
    const cnfAnswerFollowUpMessages = cnfAnswerFollowUp?.messages;
    const cnfFollowUp = cnfResponse?.followUp;
    const cnfFollowUpMessages = cnfFollowUp?.messages;
    const cnfRandomise = cnfResponse?.randomise;
    const cnfMultiChoice = cnfResponse?.multiChoice;

    const followUpMessage: ChatMessage = {
      from,
      type: 'message',
      messages: [
        ...(Array.isArray(cnfFollowUpMessages) ? cnfFollowUpMessages : []),
        ...(isFollowUp ? [followUp] : []),
      ],
    };

    log(
      {
        rowIndex: rowIndex,
        row,
        isMessage,
        isAnswer,
        isAnswerFollowUp,
        isFollowUp,
        shouldNotRandomise,
        isMultiChoice,
        cnfAnswerFollowUpMessages,
      },
      'none',
    );

    const response: FlatGameResponse | undefined =
      cnfResponse ||
      isAnswer ||
      isAnswerFollowUp ||
      isFollowUp ||
      shouldNotRandomise ||
      isMultiChoice
        ? {
            ...(cnfResponse ? cnfResponse : {}),
            options: {
              ...(cnfOptions ? cnfOptions : {}),
              ...(answerId
                ? {
                    [answerId]: {
                      ...(cnfAnswer ? cnfAnswer : {}),
                      id: answerId,
                      label: answer ? answer : cnfAnswer?.label ? cnfAnswer.label : '',
                      answer:
                        cnfAnswerFollowUp || isAnswerFollowUp
                          ? {
                              ...(cnfAnswerFollowUp ? cnfAnswerFollowUp : {}),
                              from,
                              type: 'message',
                              messages: [
                                ...(Array.isArray(cnfAnswerFollowUpMessages)
                                  ? cnfAnswerFollowUpMessages
                                  : []),
                                ...(isAnswerFollowUp
                                  ? [image ? { src: `${image}`, alt: followUp } : followUp]
                                  : []),
                              ],
                            }
                          : undefined,
                      nextStepId: nextStep !== '' ? nextStep : cnfNextStep,
                    },
                  }
                : {}),
            },
            followUp: isFollowUp ? followUpMessage : cnfFollowUp,
            randomise: shouldNotRandomise ? false : cnfRandomise,
            multiChoice: isMultiChoice ? true : cnfMultiChoice,
          }
        : undefined;

    const flatStep: FlatStep = {
      id,
      messages: {
        ...(cnf[id]?.messages ? cnf[id]?.messages : {}),
        ...(isMessage ? { [from]: chatMessage } : {}),
      },
      response,
    };

    const flatConfig: FlatConfig = {
      ...cnf,
      [id]: {
        ...(cnf[id] ? cnf[id] : {}),
        ...flatStep,
      },
    };

    return flatConfig;
  }, {} as FlatConfig);

  const config: GameContentConfig = Object.values(configObj).reduce(
    (cnf, { id, messages, response }: FlatStep) => {
      return {
        ...cnf,
        [id]: {
          id,
          messages: Object.values(messages),
          response:
            response === undefined
              ? undefined
              : {
                  options: Object.values(response.options),
                  followUp: response.followUp,
                  randomise: response.randomise,
                  multiChoice: response.multiChoice,
                },
        },
      };
    },
    {} as GameContentConfig,
  );

  return (
    <>
      <strong>{configId} </strong>
      <button
        onClick={() => {
          const config = document.getElementById(configId)?.innerHTML ?? '';
          navigator.clipboard?.writeText(config);
        }}
      >
        Copy to clipboard
      </button>

      <pre style={preStyles}>
        <code id={configId}>
          {`/* eslint-disable no-template-curly-in-string */
import { GameContentConfig } from 'types/configs';

export const game${configId}Content: GameContentConfig = `}
          {JSON.stringify(config, null, 2)}
          {`;
          `}
        </code>
      </pre>

      {/* <pre style={preStyles}>
        <code>{JSON.stringify([...configJson].reverse(), null, 2)}</code>
      </pre> */}
    </>
  );
};

export const preStyles: CSSProperties = {
  boxShadow: '0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)',
  borderRadius: '4px',
  backgroundColor: '#33394C',
  padding: '10px',
  fontSize: '0.9rem',
  maxWidth: '95vw',
  maxHeight: '100px',
  overflowY: 'auto',
};

export default ImportConfigs;
