import spinner from 'assets/static/spinner.gif';
import { useEffect, useState } from 'react';
import { Navigate, useSearchParams } from 'react-router-dom';
import { State } from 'types/state';
import { log } from 'utils';
import { lameDec } from 'utils/crypto';
import { API_URL } from '../../pages/SightSharePage';

const fetchLanding = async (respId: string, secId: string): Promise<string | undefined> => {
  const url = `${API_URL}/landing?respId=${respId}`;
  const fetchOptions = {
    method: 'GET',
    headers: {
      'x-sid': secId,
    },
  };

  log({ url, fetchOptions });

  try {
    const res = await fetch(url, fetchOptions);
    log({ res });
    if (res.status !== 200) return undefined;

    const json = await res.json();
    log({ json });
    return json.gameId;
  } catch (error) {
    log({ error });
    return undefined;
  }
};

interface LandingProps {
  respId: State['respId'];
  setLanding: (landingData: Pick<State, 'respId' | 'gameId' | 'secId'>) => void;
}

export const delay = async (ms: number) => {
  log({ delay });
  return new Promise((resolve) => setTimeout(resolve, ms));
};

export const Landing = ({ respId, setLanding }: LandingProps) => {
  const [isLoading, setIsLoading] = useState(true);
  const [params] = useSearchParams();
  const respParam = params.get('resp');

  // Landing
  useEffect(() => {
    (async () => {
      let ignore = false;
      setIsLoading(true);

      const secId = params.get(lameDec('736964'));

      log({
        op: 'useEffect.enter',
        respId,
        respParam,
        secId,
        isLoading,
        ignore,
      });

      if (!respId && respParam && secId) {
        const gameId = await fetchLanding(respParam, secId);

        log({
          op: 'setLanding',
          oldRespId: respId,
          newRespId: respParam,
          gameId,
          secId,
        });

        if (gameId) {
          setLanding({
            respId: respParam,
            gameId,
            secId,
          });
        }
      }

      setIsLoading(false);

      log({
        op: 'useEffect.exit',
        respId,
        respParam,
        secId,
        isLoading,
        ignore,
      });

      return () => {
        log({
          op: 'useEffect.dismount',
          respId,
          respParam,
          secId,
          isLoading,
          ignore,
        });
        ignore = true;
      };
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [respParam, setLanding]);

  log({ op: 'render', isLoading, respId, respParam, sid: params.get(lameDec('736964')) });

  return isLoading ? (
    <div style={{ width: '100%' }}>
      <h3>Please wait...</h3>
      <p>
        <img src={spinner} alt="Redirecting..." />
      </p>
    </div>
  ) : (
    <Navigate to="/" replace={true} />
  );
};
