import { SurveyConfig } from 'types/configs';
import { gameConfig, configCheck as gameConfigCheck } from './game';
import { checkConfigs } from './helpers';
import {
  baselineQuestionnaireConfig,
  endlineQuestionnaireConfig,
  gameProfileConfig,
} from './surveys';

export const config: SurveyConfig = {
  ...baselineQuestionnaireConfig,
  ...gameProfileConfig,
  ...gameConfig,
  ...endlineQuestionnaireConfig,
};

export const configCheck = () => {
  // eslint-disable-next-line no-console
  // console.log(JSON.stringify(Object.keys(config)));

  gameConfigCheck();
  checkConfigs(
    {
      baselineQuestionnaireConfig,
      gameProfileConfig,
      gameConfig,
      endlineQuestionnaireConfig,
    },
    config,
  );
};
