import { initialState } from 'types/state';
import { log } from './log';
import { Buffer } from 'buffer';

export const encStr = (str: string) => new TextEncoder().encode(str);
export const decStr = (str: BufferSource) => new TextDecoder().decode(str);

// See: https://stackoverflow.com/a/42321673/192331
export const getRandomIntInclusive = (min: number, max: number) => {
  const randomBuffer = new Uint32Array(1);
  window.crypto.getRandomValues(randomBuffer);

  const randomNumber = randomBuffer[0] / (0xffffffff + 1);

  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(randomNumber * (max - min + 1)) + min;
};

/*
Get some key material to use as input to the deriveKey method.
The key material is a password supplied by the user.
*/
export const getKeyMaterial = (password: string) => {
  return window.crypto.subtle.importKey('raw', encStr(password), { name: 'PBKDF2' }, false, [
    'deriveBits',
    'deriveKey',
  ]);
};

/*
Given some key material and some random salt
derive an AES-GCM key using PBKDF2.
*/
export const getKey = (keyMaterial: CryptoKey, salt: string) => {
  return window.crypto.subtle.deriveKey(
    {
      name: 'PBKDF2',
      salt: encStr(salt),
      iterations: 100000,
      hash: 'SHA-256',
    },
    keyMaterial,
    { name: 'AES-GCM', length: 256 },
    true,
    ['encrypt', 'decrypt'],
  );
};

/*
Wrap the given key.
*/
export const getCryptoKey = async () => {
  const password = '2d9b2601-cbfd-4aa7-9e2a-91d2f0bc26ac';
  const salt = '7a6b3d41-5f9b-4294-ac22-2e8cad9865e0';

  // get the key encryption key
  const keyMaterial = await getKeyMaterial(password);
  const key = await getKey(keyMaterial, salt);

  const jwk = await window.crypto.subtle.exportKey('jwk', key);

  log({ key, jwk });

  return key;
};

export const lameEnc = (s: string) => Buffer.from(encStr(s)).toString('hex');
export const lameDec = (s: string) => decStr(Buffer.from(s, 'hex').buffer);

export const generateWrappedKey = async () => {
  const key = await getCryptoKey();
  const iv = encStr('d9ab869f-4a76-462a-918f-f6c4c471f848');
  const algorithm: AesGcmParams = {
    name: 'AES-GCM',
    iv,
  };

  const obj = initialState;
  const objStr = JSON.stringify(obj);
  const objStrEnc = encStr(objStr);

  const encrypted = await window.crypto.subtle.encrypt(algorithm, key, objStrEnc);
  const encryptedJson = Buffer.from(encrypted).toJSON();
  const encryptedStr = Buffer.from(encrypted).toString('hex');

  const data = Buffer.from(encryptedStr, 'hex').buffer;
  const dataStr = Buffer.from(data).toString('hex');

  const decryptedStrEnc = await window.crypto.subtle.decrypt(algorithm, key, data);
  const decryptedStr = decStr(decryptedStrEnc);
  const decrypted = JSON.parse(decryptedStr);

  log({
    __meta: {
      key,
      iv,
      algorithm,
    },
    '1-orig': {
      obj,
      objStr,
      objStrEnc,
    },
    '2-encrypted': {
      encrypted,
      encryptedJson,
      encryptedStr,
    },
    '3-decryptData': {
      data,
      dataStr,
      dataCrc: encryptedStr === dataStr,
    },
    '4-decrypted': {
      decryptedStrEnc,
      decryptedStr,
      decryptedStrCrc: decryptedStr === objStr,
      decrypted,
    },
  });
};
