import { SurveyConfig } from 'types/configs';
import { checkConfigs, generateConfig } from '../helpers';
import { faqConfig } from '../surveys/faq';
import {
  gamePost1Content,
  gamePost2Content,
  gamePost3Content,
  gamePost4Content,
  gamePost5Content,
  gamePost6Content,
  gamePost7Content,
  gamePost8Content,
  gameQuickTasksContent,
} from './content';
import {
  gamePost1Handlers,
  gamePost2Handlers,
  gamePost3Handlers,
  gamePost4Handlers,
  gamePost5Handlers,
  gamePost6Handlers,
  gamePost7Handlers,
  gamePost8Handlers,
  gameQuickTasksHandlers,
} from './handlers';

const gamePost1Config = generateConfig(gamePost1Content, gamePost1Handlers);
const gamePost2Config = generateConfig(gamePost2Content, gamePost2Handlers);
const gamePost3Config = generateConfig(gamePost3Content, gamePost3Handlers);
const gamePost4Config = generateConfig(gamePost4Content, gamePost4Handlers);
const gamePost5Config = generateConfig(gamePost5Content, gamePost5Handlers);
const gamePost6Config = generateConfig(gamePost6Content, gamePost6Handlers);
const gamePost7Config = generateConfig(gamePost7Content, gamePost7Handlers);
const gamePost8Config = generateConfig(gamePost8Content, gamePost8Handlers);
const gameQuickTasksConfig = generateConfig(gameQuickTasksContent, gameQuickTasksHandlers);

export const gameConfig: SurveyConfig = {
  ...gamePost1Config,
  ...gamePost2Config,
  ...faqConfig,
  ...gamePost3Config,
  ...gamePost4Config,
  ...gamePost5Config,
  ...gamePost6Config,
  ...gamePost7Config,
  ...gamePost8Config,
  ...gameQuickTasksConfig,
};

export const configCheck = () => {
  checkConfigs(
    {
      gamePost1Config,
      gamePost2Config,
      faqConfig,
      gamePost3Config,
      gamePost4Config,
      gamePost5Config,
      gamePost6Config,
      gamePost7Config,
      gamePost8Config,
      gameQuickTasksConfig,
    },
    gameConfig,
    'gameConfig',
  );
};
