import { ChangeEvent, Dispatch, useEffect, useState } from 'react';
import { Navigate, useSearchParams } from 'react-router-dom';
import { StepConfig } from 'types/configs';
import { Action } from 'types/reducer';
import { State } from 'types/state';
import { log } from 'utils';
import { lameDec } from 'utils/crypto';
import {
  isStratificationGroup,
  stratificationGroups,
  testStratification,
} from 'utils/stratification';

interface Props {
  dispatch: Dispatch<Action>;
  state: State;
  configArray: StepConfig[];
}

const DevTools = ({ configArray, state, dispatch }: Props) => {
  const [stratControlPercent, setStratControlPercent] = useState(30);
  const [stratTestRuns, setStratTestRuns] = useState(120);
  const [stratResults, setStratResults] = useState<Record<string, number>>();

  const [searchParams] = useSearchParams();
  const enableDevTools =
    searchParams.get(lameDec('444f5f4e4f545f5348415245')) ===
    lameDec('33393930663337632d396364372d346336322d613433642d663139303861613533363366');

  const {
    step,
    dte: devToolsEnabled,
    dts: devToolsShown,
    version,
    responses,
    respId,
    gameId,
  } = state;
  const stepConfig = configArray[step];

  useEffect(() => {
    if (enableDevTools) dispatch({ type: 'EN_DT' });
  }, [enableDevTools, dispatch]);

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.ctrlKey === true && e.altKey === true && ['d', '∂'].includes(e.key)) {
        dispatch({ type: 'TG_DT' });
      }
    };

    if (!devToolsEnabled) {
      document.removeEventListener('keydown', handleKeyDown, false);
    }

    document.addEventListener('keydown', handleKeyDown, false);
    return () => {
      document.removeEventListener('keydown', handleKeyDown, false);
    };
  }, [devToolsEnabled, dispatch]);

  log({ step, dte: devToolsEnabled, edt: enableDevTools });

  if (enableDevTools) {
    return <Navigate to="/" replace={true} />;
  }

  if (!devToolsEnabled || !devToolsShown) {
    return <></>;
  }

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        marginTop: '30px',
        // border: '1px solid white',
        padding: '10px',
        gap: '5px',
      }}
    >
      <h2 style={{ marginBottom: '0px' }}>Dev Tools (CTRL+ALT+D)</h2>
      <div style={{ border: '1px solid white', padding: '10px', borderRadius: '10px' }}>
        <strong>Go to: </strong>
        <select
          value={step}
          onChange={(e: ChangeEvent<HTMLSelectElement>) => {
            dispatch({ type: 'GOTO_STEP', step: Number(e.target.value) });
          }}
        >
          {configArray.map((stepConfig, stepIndex) => (
            <option
              key={stepIndex}
              value={stepIndex}
              style={{
                fontWeight: stepIndex === step ? 'bold' : 'normal',
              }}
            >
              <> [step: {stepIndex}]</>
              <> [id: {stepConfig.id}]</>
              <> [type: {stepConfig.type}]</>
              {responses[stepConfig.id] && (
                <> [response: {responses[stepConfig.id].response.join('; ')}]</>
              )}
            </option>
          ))}
        </select>
        <div>
          <button onClick={() => dispatch({ type: 'RESET' })}>Restart Game</button>
        </div>
      </div>

      <div style={{ border: '1px solid white', padding: '10px', borderRadius: '10px' }}>
        <div>
          <b>Stratification Group: </b>
          <select
            value={state.stratificationGroup}
            onChange={(e: ChangeEvent<HTMLSelectElement>) => {
              const sg = e.target.value;
              if (isStratificationGroup(sg)) {
                dispatch({ type: 'SET_SG', sg });
              }
            }}
          >
            {[
              ...(state.stratificationGroup === 'unset' ? ['unset'] : []),
              ...stratificationGroups,
            ].map((stratificationGroup) => (
              <option
                key={stratificationGroup}
                value={stratificationGroup}
                style={{
                  fontWeight: stratificationGroup === state.stratificationGroup ? 'bold' : 'normal',
                }}
              >
                {stratificationGroup}
              </option>
            ))}
          </select>
        </div>
        <hr />
        <div>
          <strong>Test: </strong>
          Expected control percent:{' '}
          <input
            type="number"
            min={0}
            max={99}
            value={stratControlPercent}
            onChange={(e) => setStratControlPercent(Number(e.target.value))}
          />{' '}
          Number of test runs:{' '}
          <input
            type="number"
            min={0}
            max={100000}
            value={stratTestRuns}
            onChange={(e) => setStratTestRuns(Number(e.target.value))}
          />{' '}
          <button
            onClick={() => setStratResults(testStratification(stratControlPercent, stratTestRuns))}
          >
            Test Stratification
          </button>
          {stratResults && (
            <ul>
              {Object.entries(stratResults).map(([key, value]) => (
                <li key={key}>
                  <b>{key}:</b> {value}
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>

      <div style={{ border: '1px solid white', padding: '10px', borderRadius: '10px' }}>
        <div>
          <b>Friends:</b> {state.profile.friends} / {state.profile.initialFriends || 0}
        </div>
        {stepConfig && stepConfig.type === 'game' && (
          <div>
            <button onClick={() => dispatch({ type: 'DEC_FRIENDS' })}>Decrease Friends</button>
            <button onClick={() => dispatch({ type: 'RES_FRIENDS' })}>Reset Friends</button>
          </div>
        )}
      </div>

      <div style={{ border: '1px solid white', padding: '10px', borderRadius: '10px' }}>
        <div>
          <b>Responses:</b>
        </div>
        <ul>
          {Object.values(responses).map(({ id, response }) => (
            <li key={id}>
              <b>{id}</b>: {response.join('; ')}
            </li>
          ))}
        </ul>
      </div>

      {/* <div>
        <button onClick={generateWrappedKey}>Generate Key</button>
      </div> */}

      <div style={{ border: '1px solid white', padding: '10px', borderRadius: '10px' }}>
        <div>Resp ID: {respId}</div>
        <div>Game ID: {gameId}</div>
        <div>Env: {process.env.NODE_ENV}</div>
        <div>Version: {version}</div>
      </div>
    </div>
  );
};

export default DevTools;
